<template>
  <div style="margin: 0 auto; width: 900px">
    <div class="container">
      <div class="header">
        <div class="tab-container">
        <!-- logo vue-typed-js 自动打字 -->
          <el-tooltip content="sudo rm -rf /*" placement="bottom">
            <vue-typed-js :typeSpeed="100" :cursorChar="''" :strings="['install.icu 我们的初心，让每个开发都会运维！']">
              <span @click="toRoot" class="typing logo"></span>
            </vue-typed-js>
          </el-tooltip>
        <!-- 导航菜单 -->
          <div class="menu">
            <el-dropdown v-for="(navItem, index) in navItems" v-bind:key="index">
            <!-- <div :class="{active: activeTab == navItem.name}" @click="setActiveTab(navItem.tag)"  class="tab-child"> -->
              <div :class="{active: activeTab == navItem.name}" @click="setActiveTab(navItem, '')" class="tab-child">
                {{ navItem.name }}
              </div>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-for="(cateItem, index) in navItem.category" v-bind:key="index" @click.native="setActiveTab(navItem, cateItem)">
                  {{ cateItem.name }}
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown >
          <!-- 获取KEY -->
            <el-tooltip content="YUEEOKHGXDROKF" placement="bottom">
              <div class="tab-child-key">获取KEY</div>
            </el-tooltip>
          </div>
        </div>
      </div>
    <!-- feed 流 -->
      <div class="content-container">
        <div v-if="feedIsOpen" class="list-box-mirror">
          <div @click="feedClick(item)" class="list-item" v-for="(item, index) in feedItems" v-bind:key="index">
            <div class="item-tags">{{item.category_name}}</div>
              <div class="item-title">
                <div class="title-text">{{item.title}}</div>
              </div>
              <div class="item-status">
                <div class="status-circle success"></div>
                <div class="status-text">可用</div>
              </div>
              <div class="item-time">
                查看更多
              </div>
          </div>
        </div>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import { GetNavigation } from '@/api/api'
import { GetFeedByTag } from '@/api/api'
import util from '../../utils/utils'
// import document from 'jquery'

export default {
  name: 'Header',
  props: {
    msg: String,
  },
  data() {
    return {
      // 导航
      navItems: [],
      activeTab: '',

      // feed
      feedItems: [], // feed流数据
      feedIsOpen: true, // true则显示feed流
      detail: '',
    }
  },

  created() {
    // Detail.vue 页面返回时触发
    util.$on('detailBack', () => {
      this.feedIsOpen = true
    })
    // Detail.vue 页面触发
    util.$on('closeFeed', () => {
      this.feedIsOpen = false
    })
  },
  mounted() {
    // 判断移动端还是pc端
    if (navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)) {
      this.$router.push({path: '/m'}) // mobile
    }
    // 获取顶级导航和子分类
    this.getNavigation()
  },
  methods: {
    setActiveTab: function(navItem, cateItem) {
      // 在详情页点击顶级导航是push到/后在加载
      if (this.$route.path == '/detail') {
        this.$router.push({path: '/'})
        this.feedIsOpen = true
      }
      // 获取feed数据
      var item = {"navItem": navItem, "cateItem": cateItem};
      this.getFeed(item)

      this.activeTab = item.navItem.name
    },
    getNavigation() {
      GetNavigation().then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          var navigation = response.data.data.navigation 
          if (navigation == null) {
            this.navItems = []
            return
          }
          this.navItems = navigation // 导航列表
          this.activeTab = navigation[0].name // 取出第一个导航, activeTab使它高亮

          var navItem = {"navItem":navigation[0], "cateItem": ""}
          this.getFeed(navItem)
        }
      }).catch(err => {
        this.errMsg('获取分类列表时出错', err)
      })
    },
    getFeed(item){
      if (item.cateItem.tag) {
        this.getFeedByTag(item.navItem.tag, item.cateItem.tag); return
      }
      this.getFeedByTag(item.navItem.tag, '')
    },
    getFeedByTag(navigationTag, categoryTag) {
      GetFeedByTag(navigationTag, categoryTag).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          if (response.data.data.feed == null) {
            this.feedItems = []
            return
          }
          this.feedItems = response.data.data.feed
          return
        }
      }).catch(err => {
        this.errMsg('获取文章列表失败 ', err)
      })
    },
    feedClick(item) {
      this.feedIsOpen = false
      // this.feedItems = []
      // push 到详情页
      this.$router.push({
        path:'detail',
        query: {item: JSON.stringify(item)}
      });
    },
    toRoot() {
      this.$router.push({path: '/'})
      this.feedIsOpen = true
    },
    errMsg(msg, err) {
      this.$message({
        type: 'error',
        message: msg + err
      });
    },
  }
}
</script>

<style scoped>
.container {
  width: 100%;
  height: 100%;
}

.header {
  width: 100%;
  height: 30px;
  margin-bottom: 15px;
}

.tab-container {
  display: flex;
  width: 100%;
  height: 30px;
}

.logo {
  height: 30px;
  line-height: 30px;
  text-align: center;
  margin-right: 10px;
  color: #19001d;
  font-size: 18px;
  letter-spacing: 0;
  border-radius: 2px;
  cursor: pointer;
  transition: all 0.2s linear;
}

.logo:hover {
  color:#f60;
  transition: all 0.2s linear;
}

.menu {
  display: flex;
  margin-left: auto
}

/* .el-dropdown {
  vertical-align: top;
} */
.el-dropdown + .el-dropdown {
  margin-left: 15px;
}
/* .el-icon-arrow-down {
  font-size: 12px;
}  */
.el-dropdown-menu__item:not(.is-disabled):hover{
  background-color: #f60 !important;
  color: #fff !important;
}

.tab-child {
  padding: 1px 18px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  margin-left: 10px;
  color: #19001d;
  font-size: 14px;
  letter-spacing: 0;
  border-radius: 2px;
  cursor: pointer;
}
.active,.tab-child:hover {
  color: #fff;
  background: #f60;
  border: 0;
  
}

.tab-child-key  {
  padding: 1px 18px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  margin-left: 10px;
  color: #19001d;
  font-size: 14px;
  letter-spacing: 0;
  border-radius: 2px;
  cursor: pointer;
}
.tab-child-key:hover {
  color:#f60;
  transition: all 0.2s linear;
}

div:focus{
	outline: none;
}

/* feed */
.content-container {
  margin: 0 auto;
  width: 100%;
}

.list-item {
  float: left;
  height: 89px;
  border: 1px solid #eaeaea;
  margin-top: 20px;
  background: #fff;
  position: relative;
  width: 21%;
  margin: 20px 1% 0 1%;
  padding: 2% 5% 0
}

.item-tags {
  position: absolute;
  left: 0;
  top: 0;
  width: 30px;
  height: 17px;
  background: #ff6a00;
  border-radius: 0 0 2px 0;
  font-family: PingFangSC-Medium;
  font-size: 12px;
  color: #fff;
  letter-spacing: 0;
  text-align: center;
  line-height: 17px;
  overflow: hidden;
}
.item-title {
  height: 25px;
}

.item-status {
  overflow: hidden;
  letter-spacing: 0;
  height: 24px;
  line-height: 24px;
  margin: 3px 0 2px;
}

.success {
  background: #50be61;
}
.status-circle {
  float: left;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  margin-top: 10px;
  margin-right: 10px;
}

.status-text {
  float: left;
  font-size: 14px;
  color: #373d41;
}
.item-time {
  font-size: 12px;
  color: #73777a;
  letter-spacing: 0;
  line-height: 24px;
}
.list-item:hover {
  box-shadow: 0 2px 15px 0 rgba(0,0,0,.15);
  cursor: pointer
}
.list-item:hover .item-title .title-text {
  color: #ff6a00;
}

a:visited {
  color: #00b7d3;
}

/* 去除a 标签默认样式*/
/*包含以下四种的链接*/
a {
  text-decoration: none;
}
/*正常的未被访问过的链接*/
a:link {
  text-decoration: none;
}
/*已经访问过的链接*/
a:visited {
  text-decoration: none;
}
/*鼠标划过(停留)的链接*/
a:hover {
  text-decoration: none;
}
/* 正在点击的链接*/
a:active {
  text-decoration: none;
}
</style>
