import Vue from 'vue'
import App from './App.vue'
import router from './router'
import ElementUI from 'element-ui'
import VueTypedJs from 'vue-typed-js' // 自动打字插件
import Contextmenu from "vue-contextmenujs" // 前台右击菜单组件
import 'element-ui/lib/theme-chalk/index.css' // 后台 element-ui CSS
import 'github-markdown-css/github-markdown.css'  // 前台 markdown CSS

Vue.use(ElementUI);
Vue.use(Contextmenu);
Vue.use(VueTypedJs)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
