import request from '@/utils/request'

export function GetFeedByTag (navigationTag, categoryTag) {
  return request({
    url: '/api/v1/feeds',
    method: 'get',
    headers: {
      'Navigation-Tag': navigationTag,
      'Category-Tag': categoryTag,
    },
  })
}

export function UpdateArticle (id, title, detail, navTag, navName, categoryTag, categoryName) {
  return request({
    url: '/api/v1/articles/' + id,
    method: 'put',
    headers: {'Token': localStorage.getItem('token')},
    data: {
      'title': title,
      'detail': detail,
      'navigation_tag': navTag,
      'navigation_name': navName,
      'category_tag': categoryTag,
      'category_name': categoryName
    }
  })
}

export function DelArticle (id) {
  return request({
    url: '/api/v1/articles/' + id,
    method: 'delete',
    headers: {'Token': localStorage.getItem('token')},
  })
}

