import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/frontend/Index.vue'
import { Login } from '@/api/api'

Vue.use(VueRouter)

// avoided redundant navigation to current location 报错解决
// const originalPush = VueRouter.prototype.push
//    VueRouter.prototype.push = function push(location) {
//    return originalPush.call(this, location).catch(err => err)
// }

const routes = [
  {
    path: '/',
    name: 'Index',
    component: Index,
    children: [
      {
        path: 'detail',
        name: 'Detail',
        component: () => import('../views/frontend/Detail.vue'),
      },
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/admin/Login.vue')
  },
  {
    path: '/m',
    name: 'M',
    component: () => import('../views/mobile/Index.vue'),
    meta: {
      keepAlive: true
    }
  },
  {
    path: '/m/detail',
    name: 'Detail',
    component: () => import('../views/mobile/Detail.vue'),
  },
  {
    path: '/admin',
    name: 'Menu',
    component: () => import('../views/admin/Menu.vue'),
    children: [
      { path: '/admin', redirect: '/admin/editor' },
      {
        path: 'editor',
        name: 'Editor',
        component: () => import('../views/admin/editor/Editor.vue')
      },
      {
        path: 'article_mgt',
        name: 'ArticleMgt',
        component: () => import('../views/admin/article_mgt/ArticleMgt.vue')
      },
    ]
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router

// 全局路由前置守卫
router.beforeEach((to, from, next) => {
  var toPathRegExp = /admin.*/g;
  var res = toPathRegExp.test(to.path);

  if (res == true) {
    Login('', '', localStorage.getItem('token')).then(response => {
      if (response.data.code === 0) {
        next() // 通过方行
      } else { // 没有通过重定向到/login
        // next('/login?redirect='+ to.path);
        // next('/login?redirect=/dashboard/mine');
        next('/login')
      }
    }).catch(err => {
      console.log(err)
      next('/login')
    })
  } else { // 放行/login
    next()
  }
})
